<template>
  <nav aria-label="breadcrumb">
    <ol class="breadcrumb">
      <li class="breadcrumb-item"><a class="link-primary" href="/"></a></li>
      <li class="breadcrumb-item" v-if="theme">
        <a class="link-primary" :href="`/searching?theme=${apiType}`">{{ theme }}</a>
      </li>
      <li class="breadcrumb-item" v-if="city && city != ''">
        <a class="link-primary" :href="`/searching?theme=${apiType}&city=${city}`">{{ city }}</a>
      </li>
      <li class="breadcrumb-item" v-if="category && category != ''">
        <a class="link-primary" :href="`/searching?theme=${apiType}&category=${category}`">{{ category }}</a>
      </li>
      <li class="breadcrumb-item link-primary active" aria-current="page" v-if="name">
        {{ name }}
      </li>
    </ol>
  </nav>
</template>
<script>
export default {
  data() {
    return {
      theme: "",
    };
  },
  props: ["apiType", "city", "name", "category"],
  created() {
    switch (this.apiType) {
      case "ScenicSpot":
        this.theme = "";
        break;
      case "Activity":
        this.theme = "";
        break;
      case "Restaurant":
        this.theme = "";
        break;
    }
  },
};
</script>
